<!--
 * @Description: 修改账号信息弹出框模块
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-05 13:42:39
 * @LastEditTime: 2019-09-25 15:13:57
 -->
<template>
  <div >
    <v-layout row justify-center>
      <v-dialog v-model="dialog" max-width="600px">
        <!-- <template v-slot:activator="{ on }">
          <v-btn color="primary" flat dark v-on="on"> + 创建管理/代理人员账号</v-btn>
        </template> -->
        <v-card>
          <v-card-title>
            <span class="headline">账号详细信息</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>

              <p class="root_p" > <span class="order_title">登录账号</span>  <v-chip>{{ data.user_name }}</v-chip> </p>
              <p class="root_p" > <span class="order_title">登录密码</span>  <v-chip> ******** </v-chip> </p>
              <p class="root_p" > <span class="order_title">真实姓名</span>  <v-chip>{{ data.true_name }}</v-chip> </p>
              <p class="root_p" > <span class="order_title">性别</span>  <v-chip>{{ data.gender==1?"男":"女"  }}</v-chip> </p>
              <p class="root_p" > <span class="order_title">角色</span>  <v-chip>{{ data.role_name }}</v-chip> </p>
              <p class="root_p" > <span class="order_title">手机号码</span>  <v-chip>{{ data.phone | cover }}</v-chip> </p>
              <p class="root_p" > <span class="order_title">邮箱</span>  <v-chip>{{ data.email }}</v-chip> </p>
              <p class="root_p" > <span class="order_title">创建时间</span>  <v-chip>{{ data.create_time | time }}</v-chip> </p>
              <p class="root_p" > <span class="order_title">修改时间</span>  <v-chip>{{ data.update_time | time }}</v-chip> </p>
              

            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <!-- <v-btn color="blue darken-1" flat @click="dialog = false">取消</v-btn> -->
            <v-btn color="info darken-1" @click="dialog = false">确认</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
    
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
        data:{
            create_time: "",
            email: "",
            gender: "",
            id: "",
            order_sn: "",
            phone: "",
            role_id: "",
            role_name: "",
            status: "",
            true_name: "",
            update_time: "",
            user_name: ""
        }
    };
  },
  // 更新完成
  mounted: function() {
    
  },
  methods: {
    dialogShow(data){
        this.dialog = data.state;
        this.data = data.list;
    }
  },
};
</script>

<style lang="less" scoped>

.root_p{
    font-size: 16px;
}

.order_title{
  display: inline-block;
  width: 125px;
}

</style>
