<!--
 * @Description: 代理列表
 * @LastEditors: Ji Yinghao
 * @Date: 2019-04-30 12:52:55
 * @LastEditTime: 2019-10-09 18:41:31
 -->
<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items" />
    <!-- 数据表格 -->
    <v-card>
      <v-card-title>
        <v-layout row wrap>
          <!-- <v-flex xs6 sm4 md2>
            <v-text-field v-model="eee" label="账号"></v-text-field>
          </v-flex>-->
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="NAME" label="真实姓名"></v-text-field>
          </v-flex>
          <!-- <v-btn :loading="loader" :disabled="loader" color="primary" @click="loader_">搜索</v-btn> -->
          <v-spacer></v-spacer>
          <v-flex xs2>
            <!-- <v-card dark color="accent"> 填充 -->
            <template v-for="(item,index) in root_path">
              <Module :key="index" v-if="item.url == '/api/v1/manages/add_manage'"></Module>
            </template>
            <Module1 ref="module1"></Module1>
            <Module2 ref="module2"></Module2>
            <!-- </v-card> -->
          </v-flex>

          <!-- <v-btn color="primary" dark v-on="on">添加子账号</v-btn> -->
        </v-layout>
      </v-card-title>
      <!-- 代理账号列表
          headers : 表头
          items： 表格内容
          total-items ： 总共的数据数量 （手动设置项目的总数，就可以禁用内置排序和分页）
          disable-initial-sort 在初始渲染时禁用默认排序
          :hide-actions="true" 隐藏表格操作
          :dark="true" 暗主题
          :loading="true" 加载进度条
      -->
      <v-data-table
        :headers="headers"
        :items="desserts_"
        class="elevation-1"
        :total-items="0"
        :rows-per-page-text="`每页行数`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        @update:pagination="info"
      >
        <template v-slot:items="props">
          <tr @dblclick="infoMessage( props.item )">
            <td>{{ props.index + 1 }}</td>
            <td class="text-xs-centre">{{ props.item.user_name }}</td>
            <td class="text-xs-centre">{{ props.item.true_name }}</td>
            <td class="text-xs-centre">{{ props.item.role_name }}</td>
            <td class="text-xs-centre">{{ props.item.phone | cover }}</td>
            <td class="text-xs-centre">{{ props.item.email }}</td>
            <td class="text-xs-centre">{{ props.item.create_time | time }}</td>
            <td class="text-xs-centre">
              <v-menu
                bottom
                dark
                origin="center center"
                :close-delay="100"
                transition="slide-x-transition"
              >
                <template v-slot:activator="{ on }">
                  <v-btn color="primary" dark v-on="on">代理操作选项</v-btn>
                </template>
                <v-list>
                  <v-list-tile @click="infoMessage( props.item )">
                    <v-list-tile-title>代理详情</v-list-tile-title>
                  </v-list-tile>

                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index"
                      v-if="item.url == '/api/v1/manages/modify_manage'"
                      @click="modifyMessage( props.item )"
                    >
                      <v-list-tile-title>代理修改</v-list-tile-title>
                    </v-list-tile>
                  </template>

                  <v-list-tile @click="user_list( props.item.user_name )">
                    <v-list-tile-title>下属用户</v-list-tile-title>
                  </v-list-tile>

                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index"
                      v-if="item.url == '/api/v1/manages/delete_manage'"
                      @click="delMessage( props.item.id )"
                    >
                      <v-list-tile-title class="red--text">删除代理</v-list-tile-title>
                    </v-list-tile>
                  </template>
                </v-list>
              </v-menu>
              <!-- <v-btn small color="primary" @click="infoMessage( props.item )" >详情</v-btn>
            <v-btn small color="primary" @click="modifyMessage( props.item )" >修改</v-btn>
              <v-btn small color="error" @click="delMessage( props.item.id )" >删除</v-btn>-->
            </td>
          </tr>
        </template>
        <!-- 当没有数据的时候，可以使用 no-data 插槽显示一段自定义HTML。 -->
        <template v-slot:no-data>
          <!-- <v-alert :value="true" color="warning" icon="priority_high" outline>没有更多数据了 :(</v-alert> -->
          <p class="p_text">没有更多数据了 :(</p>
        </template>
        <!-- 如果你还想添加一些额外的功能，比如每列过滤或者搜索，你可以使用 footer 插槽。 -->
        <!-- <template v-slot:footer>
          <td :colspan="headers.length">
            <strong>总计：</strong>
          </td>
        </template>-->
        <!-- 你可以使用page-text 插槽来自定义显示范围以及总条目。 -->
        <template
          v-slot:pageText="props"
        >当前 {{ props.pageStart }} - {{ props.pageStop }} 总共 {{ props.itemsLength }}</template>
      </v-data-table>
    </v-card>
    <!-- 确认框 -->
    <v-dialog v-model="dialog_logout" max-width="290">
      <v-card>
        <v-card-title class="headline">删除操作</v-card-title>
        <v-card-text>确认删除该账号吗？确认删除将不可恢复</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat="flat" @click="dialog_logout = false">取消</v-btn>
          <v-btn color="green darken-1" flat="flat" @click="delete_manage">确认删除</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Module from "../components/module.vue";
import Module1 from "../components/module1.vue";
import Module2 from "../components/module2.vue";
import Api from "../../../server/api.js";
export default {
  components: {
    Breadcrumbs,
    Module,
    Module1,
    Module2
  },
  data() {
    return {
      NAME: "",
      // 确认框
      dialog_logout: false,
      id: null,

      pagingList: [100, { text: "100", value: 100 }],
      loader: false,
      page: 0,
      items: [
        {
          text: "首页",
          disabled: false,
          to: "/home"
        },
        {
          text: "代理人员列表",
          disabled: false,
          to: "/agentList"
        }
      ],
      headers: [
        {
          text: "#",
          align: "left",
          sortable: false, // 是否可排序
          value: "name"
        },
        { text: "账号", value: "fat", sortable: false },
        { text: "真实姓名", value: "fat", sortable: false },
        { text: "角色", value: "calories", sortable: false },
        { text: "手机", value: "carbs", sortable: false },
        { text: "邮箱", value: "protein", sortable: false },
        { text: "创建时间", value: "iron", sortable: false },
        { text: "操作", value: "iron", sortable: false }
      ],
      desserts: [],
      root_path: this.$cookie.getLocalStorage("root_path").root_path
    };
  },
  computed: {
    // 前端筛选
    desserts_: function() {
      return this.desserts.filter(blog => {
        return blog.true_name.match(this.NAME);
      });
    }
  },
  methods: {
    loader_() {
      this.loader = true;
      setTimeout(() => (this.loader = false), 500);
    },
    info() {
      this.loader = true;
      this.$axios({
        method: "post",
        url: Api.URL.manages_query_manage,
        data: {
          id: -1,
          level: 2
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.desserts = response.data.datas;
          } else {
            this.$Dialog.getProductCheckResult_({
              text: response.data.message,
              timeout: 3000,
              snackbar: true
            });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
            text: "😡" + error,
            timeout: 3000,
            snackbar: true
          });
        });
    },
    // 子组件中触发
    adminAdd() {
      this.info();
    },
    // 修改账号信息
    modifyMessage(data) {
      let datas = {
        state: true,
        list: data
      };
      this.$refs.module2.dialogShow(datas);
    },
    // 下属用户列表
    user_list(agentName) {
      this.$router.push({
        path: `/userList`,
        query: {
          agent_name: agentName
        }
      });
    },
    // 查看账号详情
    infoMessage(data) {
      let datas = {
        state: true,
        list: data
      };
      this.$refs.module1.dialogShow(datas);
    },
    // 删除确认框
    delMessage(id) {
      this.id = id;
      this.dialog_logout = true;
    },
    // 删除员工或代理账号 manages/delete_manage
    delete_manage() {
      this.$axios({
        method: "post",
        url: Api.URL.manages_delete_manage,
        data: {
          id: this.id
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({
              text: "账号已成功删除!",
              timeout: 3000,
              snackbar: true
            });
            this.info();
            this.dialog_logout = false;
          } else {
            this.$Dialog.getProductCheckResult_({
              text: response.data.message,
              timeout: 3000,
              snackbar: true
            });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
            text: "😡" + error,
            timeout: 3000,
            snackbar: true
          });
        });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.flex {
  padding-right: 20px;
  padding-left: 20px;
}
</style>
